/* Main container styling */
.person-details-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 30px;
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  min-height: 100vh;
  font-family: 'Arial', sans-serif;
  color: #fff;
}



/* Person card container */
.person-card {
  width: 90%;
  max-width: 1000px;
  background: #ffffff;
  color: #333;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  position: relative;
  animation: fadeIn 1s ease-in-out;
}

/* Image styling */
.person-image {
  width: 100%;
  max-width: 350px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* Info section styling */
.person-info {
  flex: 1;
  padding: 20px;
}

.person-info h1 {
  font-size: 2rem;
  color: #6a11cb;
  margin-bottom: 10px;
}

.person-info p {
  font-size: 1rem;
  margin-bottom: 15px;
  line-height: 1.5;
}

.person-info a {
  color: #2575fc;
  font-weight: bold;
  text-decoration: none;
}

.person-info a:hover {
  text-decoration: underline;
}

/* Video styling */
.person-video {
  width: 100%;
  max-width: 400px;
  margin: 20px auto;
}

.person-video h2 {
  font-size: 1.2rem;
  margin-bottom: 10px;
  text-align: center;
}

/* Reviews container styling */
.reviews-container {
  width: 100%;
  background: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.reviews-container h2 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #6a11cb;
}

.reviews-container p {
  font-size: 1rem;
  margin-bottom: 10px;
}

.review-list {
  list-style: none;
  padding: 0;
}

.review-list li {
  padding: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid #ddd;
}

.review-list li:last-child {
  border-bottom: none;
}

/* Review form styling */
.review-form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.review-form label {
  font-size: 1rem;
  color: #333;
}

.review-form select,
.review-form textarea {
  width: 100%;
  max-width: 500px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  color: #333;
  background: #f7f7f7;
}

.review-form textarea {
  resize: none;
}

.review-form button {
  width: 100%;
  max-width: 200px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  color: #fff;
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  transition: background 0.3s ease;
}

.review-form button:hover {
  background: linear-gradient(135deg, #2575fc, #6a11cb);
}

.review-form .but {
  background: #f44336;
  border: none;
}

.review-form .but:hover {
  background: #d32f2f;
}

/* Animations */
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Media query for responsiveness */
@media (max-width: 768px) {
  .person-card {
    flex-direction: column;
    align-items: center;
  }

  .person-video {
    width: 100%;
  }

  .person-info {
    text-align: center;
  }

  .review-form {
    align-items: center;
  }
}
